"use client";

import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { PlaceHolderImages } from "@/lib/placeholder-images";
import { GlassCard, CardContent, CardHeader } from "./glass-card";
import { GradientOrb } from "./gradient-orb";
import * as React from "react";
import { Star } from "lucide-react";
import { useGSAP, prefersReducedMotion } from "@/lib/gsap-utils";

const testimonials = [
  {
    name: "Alex Johnson",
    title: "Aspiring Developer",
    testimonial:
      "VoyLearning made HTML so approachable! The notes are incredibly clear and helped me build my first website.",
    avatar: PlaceHolderImages.find((p) => p.id === "testimonial-1"),
    initials: "AJ",
    speed: 1,
  },
  {
    name: "Jessica Lee",
    title: "Student",
    testimonial:
      "I was struggling with other resources, but the structured path on VoyLearning kept me on track. Highly recommend!",
    avatar: PlaceHolderImages.find((p) => p.id === "testimonial-3"),
    initials: "JL",
    speed: 0.8,
  },
  {
    name: "Mike P.",
    title: "Career Changer",
    testimonial:
      "The concise notes saved me so much time. I finally feel confident in my HTML skills thanks to this platform.",
    avatar: PlaceHolderImages.find((p) => p.id === "testimonial-2"),
    initials: "MP",
    speed: 1.2,
  },
];

export default function SocialProofSection() {
  const ref = React.useRef<HTMLDivElement | null>(null);

  useGSAP(() => {
    if (prefersReducedMotion()) return;
    const root = ref.current;
    if (!root) return;
    import("gsap/dist/gsap").then(({ gsap }) => {
      import("gsap/dist/ScrollTrigger").then(({ ScrollTrigger }) => {
        gsap.registerPlugin(ScrollTrigger);
        const cards = root.querySelectorAll("[data-t-card]");
        gsap.fromTo(
          cards,
          { opacity: 0, y: 20, rotateZ: -2 },
          {
            opacity: 1,
            y: 0,
            rotateZ: 0,
            stagger: 0.15,
            duration: 0.6,
            ease: "power2.out",
            scrollTrigger: { trigger: root, start: "top 80%" },
          }
        );

        cards.forEach((c, i) => {
          const speed = testimonials[i]?.speed ?? 1;
          gsap.to(c, {
            yPercent: -speed * 10,
            ease: "none",
            scrollTrigger: {
              trigger: root,
              scrub: true,
              start: "top bottom",
              end: "bottom top",
            },
          });
        });
      });
    });
  });

  return (
    <section id="testimonials" className="relative w-full bg-secondary py-24 md:py-32 overflow-hidden">
      <GradientOrb color="purple" size="lg" position={{ top: "-4rem", left: "-4rem" }} opacity={0.2} />
      <GradientOrb color="blue" size="md" position={{ bottom: "-3rem", right: "-3rem" }} opacity={0.2} />
      <div className="container px-4 md:px-6" ref={ref}>
        <div className="mb-12 flex flex-col items-center justify-center space-y-4 text-center">
          <h2 className="text-4xl font-bold tracking-tighter sm:text-5xl text-gradient">Join Fellow Learners on Their Voyage</h2>
          <p className="max-w-[900px] text-muted-foreground md:text-xl/relaxed lg:text-base/relaxed xl:text-xl/relaxed">
            See how VoyLearning is helping people from all backgrounds master web development.
          </p>
        </div>
        <div className="grid grid-cols-1 gap-8 sm:grid-cols-2 lg:grid-cols-3">
          {testimonials.map((t, i) => (
            <GlassCard key={i} className="relative overflow-hidden" hover gradient data-t-card>
              <CardHeader>
                <div className="pointer-events-none absolute left-3 top-0 text-6xl opacity-20 text-gradient select-none">â€œ</div>
                <div className="flex items-center gap-4">
                  <div className="rounded-full ring-2 ring-offset-2 ring-offset-background ring-purple-500">
                    <Avatar>
                      {t.avatar && (
                        <AvatarImage src={t.avatar.imageUrl} alt={t.avatar.description} data-ai-hint={t.avatar.imageHint} />
                      )}
                      <AvatarFallback className="bg-gradient-to-r from-purple-500 to-pink-500 text-white">{t.initials}</AvatarFallback>
                    </Avatar>
                  </div>
                  <div>
                    <p className="font-semibold">{t.name}</p>
                    <p className="text-sm text-muted-foreground">{t.title}</p>
                  </div>
                </div>
              </CardHeader>
              <CardContent>
                <div className="mb-3 flex text-primary">
                  {Array.from({ length: 5 }).map((_, idx) => (
                    <Star key={idx} className="h-5 w-5 fill-yellow-400 text-yellow-400" />
                  ))}
                </div>
                <p className="text-muted-foreground">â€œ{t.testimonial}â€</p>
              </CardContent>
            </GlassCard>
          ))}
        </div>
      </div>
    </section>
  );
}
