"use client";

import { Layers, CheckCircle, Rocket, Smile } from "lucide-react";
import * as React from "react";
import { GlassCard, CardContent } from "./glass-card";
import DotPattern from "./dot-pattern";
import { useGSAP, prefersReducedMotion } from "@/lib/gsap-utils";

const benefits = [
  {
    icon: <Layers className="h-10 w-10 text-white" />,
    title: "Build a Solid Foundation",
    description: "Master the core principles of HTML to build a strong base for your web development career.",
  },
  {
    icon: <CheckCircle className="h-10 w-10 text-white" />,
    title: "Understand Concepts Clearly",
    description: "Our notes break down complex topics into simple, digestible pieces of information.",
  },
  {
    icon: <Rocket className="h-10 w-10 text-white" />,
    title: "Accelerate Your Learning",
    description: "A structured path and quality resources help you learn faster and more effectively.",
  },
  {
    icon: <Smile className="h-10 w-10 text-white" />,
    title: "Gain Confidence",
    description: "Go from aspiring developer to a confident creator, ready to build your own websites.",
  },
];

export default function BenefitsSection() {
  const scopeRef = useGSAP(async (ctx) => {
    if (prefersReducedMotion()) return;
    const { gsap } = await import('gsap');
    await import('gsap/ScrollTrigger');
    const items = ctx.selector?.('[data-benefit-item]') as NodeListOf<Element> | undefined;
    if (!items || !items.length) return;
    gsap.fromTo(items, { opacity: 0, y: 16 }, { opacity: 1, y: 0, duration: 0.5, stagger: 0.15, ease: 'power2.out', scrollTrigger: { trigger: ctx.scope as Element, start: 'top 80%' } });
  });

  return (
    <section ref={scopeRef as any} id="benefits" className="relative w-full bg-gradient-to-b from-background to-gray-50 py-24 md:py-32 overflow-hidden">
      <DotPattern className="opacity-30" />
      <div className="container px-4 md:px-6">
        <div className="mb-12 flex flex-col items-center justify-center space-y-4 text-center">
          <h2 className="text-4xl font-bold tracking-tighter sm:text-5xl text-gradient">Transform Your Skills</h2>
            <p className="max-w-[900px] text-muted-foreground md:text-xl/relaxed lg:text-base/relaxed xl:text-xl/relaxed">
              With VoyLearning, you're not just learning to code, you're building a new future.
            </p>
        </div>
        <div className="mx-auto grid max-w-5xl items-start gap-8 py-6 lg:grid-cols-2 lg:gap-12">
          <div className="grid gap-6">
            {benefits.slice(0,2).map((benefit) => (
              <GlassCard key={benefit.title} className="p-4 hover:shadow-premium-lg hover:scale-105 transition" hover data-benefit-item>
                <CardContent className="p-0">
                  <div className="flex items-start gap-4">
                    <div className="flex h-16 w-16 items-center justify-center rounded-full motion-safe:animate-float" style={{ backgroundImage: 'var(--gradient-3)' }}>
                      {benefit.icon}
                    </div>
                    <div>
                      <h3 className="text-2xl font-bold"><span className="text-gradient">{benefit.title.split(' ')[0]}</span> {benefit.title.replace(benefit.title.split(' ')[0] + ' ', '')}</h3>
                      <p className="text-muted-foreground">{benefit.description}</p>
                    </div>
                  </div>
                </CardContent>
              </GlassCard>
            ))}
          </div>
          <div className="grid gap-6">
            {benefits.slice(2,4).map((benefit) => (
              <GlassCard key={benefit.title} className="p-4 hover:shadow-premium-lg hover:scale-105 transition" hover data-benefit-item>
                <CardContent className="p-0">
                  <div className="flex items-start gap-4">
                    <div className="flex h-16 w-16 items-center justify-center rounded-full motion-safe:animate-float" style={{ backgroundImage: 'var(--gradient-3)' }}>
                      {benefit.icon}
                    </div>
                    <div>
                      <h3 className="text-2xl font-bold"><span className="text-gradient">{benefit.title.split(' ')[0]}</span> {benefit.title.replace(benefit.title.split(' ')[0] + ' ', '')}</h3>
                      <p className="text-muted-foreground">{benefit.description}</p>
                    </div>
                  </div>
                </CardContent>
              </GlassCard>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
}
