"use client";

import React from "react";
import { cn } from "@/lib/utils";

type OrbColor = "purple" | "blue" | "pink" | "green";
type OrbSize = "sm" | "md" | "lg" | "xl";

interface Position {
  top?: string | number;
  left?: string | number;
  right?: string | number;
  bottom?: string | number;
}

interface GradientOrbProps extends React.HTMLAttributes<HTMLDivElement> {
  color?: OrbColor;
  size?: OrbSize;
  position?: Position;
  opacity?: number;
  blur?: boolean;
  animate?: boolean;
}

const sizeMap: Record<OrbSize, string> = {
  sm: "w-24 h-24",
  md: "w-40 h-40",
  lg: "w-64 h-64",
  xl: "w-96 h-96",
};

const colorMap: Record<OrbColor, string> = {
  purple: "var(--gradient-1)",
  pink: "linear-gradient(135deg, #ec4899, #a78bfa)",
  blue: "linear-gradient(135deg, #60a5fa, #06b6d4)",
  green: "linear-gradient(135deg, #34d399, #22c55e)",
};

export function GradientOrb({
  color = "purple",
  size = "md",
  position,
  opacity = 0.3,
  blur = true,
  animate = true,
  className,
  ...props
}: GradientOrbProps) {
  const style: React.CSSProperties = {
    backgroundImage: colorMap[color],
    opacity,
    filter: blur ? "blur(40px)" : undefined,
    ...position,
  };

  return (
    <div
      aria-hidden
      className={cn(
        "pointer-events-none absolute rounded-full mix-blend-screen",
        sizeMap[size],
        animate && "motion-safe:animate-float gpu",
        className
      )}
      style={style}
      {...props}
    />
  );
}

export default GradientOrb;
