"use client";

import * as React from "react";
import DotPattern from "./dot-pattern";
import { GradientOrb } from "./gradient-orb";
import { useGSAP, prefersReducedMotion } from "@/lib/gsap-utils";

export default function TextRevealSection() {
  const scopeRef = useGSAP(async (ctx) => {
    if (prefersReducedMotion()) return;
    const { gsap } = await import('gsap');
    const { ScrollTrigger } = await import('gsap/ScrollTrigger');
    gsap.registerPlugin(ScrollTrigger);

    const container = ctx.scope as Element;
    const lines = (ctx.selector?.('[data-line]') as NodeListOf<Element>) || [];
    gsap.set(lines, { opacity: 0, y: 30 });
    const isDesktop = typeof window !== 'undefined' && window.innerWidth >= 768;
    ScrollTrigger.create({
      trigger: container,
      start: isDesktop ? "top center" : "top 80%",
      end: isDesktop ? "+=60%" : "top center",
      pin: isDesktop,
      scrub: isDesktop,
      onEnter: () => {
        gsap.to(lines, { opacity: 1, y: 0, stagger: 0.3, ease: "power2.out", duration: 0.6 });
      },
    });
  });

  return (
    <section ref={scopeRef as any} className="relative w-full overflow-hidden bg-gradient-to-b from-gray-900 to-black py-32 md:py-40">
      <DotPattern className="opacity-10" />
      <GradientOrb color="purple" size="lg" position={{ top: "-6rem", left: "-6rem" }} opacity={0.15} />
      <GradientOrb color="blue" size="md" position={{ bottom: "-4rem", right: "-4rem" }} opacity={0.15} />
      <div className="container px-4 md:px-6 relative z-10">
        <div className="mx-auto max-w-4xl text-center text-gray-100 space-y-6">
          <p data-line className="text-3xl md:text-5xl lg:text-6xl font-bold leading-tight tracking-tight">
            Learning shouldn't be <span className="text-gradient">passive</span>.
          </p>
          <p data-line className="text-3xl md:text-5xl lg:text-6xl font-bold leading-tight tracking-tight">
            Your notes should <span className="text-gradient-blue">grow</span> with you.
          </p>
          <p data-line className="text-3xl md:text-5xl lg:text-6xl font-bold leading-tight tracking-tight">
            <span className="text-gradient-pink">AI</span> makes it possible.
          </p>
        </div>
      </div>
    </section>
  );
}
